/* Estilos globais */
body {
  background-color: #FFFFFF;
  overflow-x: hidden;
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

section, .section {
  padding: 4rem 0;
}
section.bg-light, .section.bg-light {
  background-color: #f8f8f8;
}
section.bg-dark, .section.bg-dark {
  background-color: #800020;
  color: #ffffff;
}
section.bg-dark h1, section.bg-dark h2, section.bg-dark h3, section.bg-dark h4, section.bg-dark h5, section.bg-dark h6, .section.bg-dark h1, .section.bg-dark h2, .section.bg-dark h3, .section.bg-dark h4, .section.bg-dark h5, .section.bg-dark h6 {
  color: #ffffff;
}
section.bg-dark a, .section.bg-dark a {
  color: #ffd700;
}
section.bg-dark a:hover, section.bg-dark a:focus, .section.bg-dark a:hover, .section.bg-dark a:focus {
  color: #ffdf33;
}
section.section-sm, .section.section-sm {
  padding: 2rem 0;
}
section.section-lg, .section.section-lg {
  padding: 6rem 0;
}
section.padding-half, .section.padding-half {
  padding: 2rem 0;
}

.section-title {
  text-align: center;
  margin-bottom: 3rem;
}
.section-title h2 {
  color: #800020;
  font-size: 2rem;
  position: relative;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
  display: inline-block;
}
.section-title h2::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background-color: #ffd700;
}
.section-title p {
  max-width: 700px;
  margin: 0 auto;
  color: #4a4a4a;
}
.section-title.title-left {
  text-align: left;
}
.section-title.title-left h2::after {
  left: 0;
  transform: none;
}
.section-title.title-left p {
  margin: 0;
}
.section-title.light h2 {
  color: #ffffff;
}
.section-title.light p {
  color: rgba(255, 255, 255, 0.8);
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.col {
  padding: 0 15px;
  width: 100%;
}
@media (min-width: 768px) {
  .col-md-6 {
    width: 50%;
  }
}
@media (min-width: 768px) {
  .col-md-4 {
    width: 33.333333%;
  }
}
@media (min-width: 768px) {
  .col-md-3 {
    width: 25%;
  }
}
@media (min-width: 992px) {
  .col-lg-4 {
    width: 33.333333%;
  }
}
@media (min-width: 992px) {
  .col-lg-3 {
    width: 25%;
  }
}

.grid {
  display: grid;
  gap: 30px;
}
.grid-2 {
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .grid-2 {
    grid-template-columns: repeat(2, 1fr);
  }
}
.grid-3 {
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .grid-3 {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .grid-3 {
    grid-template-columns: repeat(3, 1fr);
  }
}
.grid-4 {
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .grid-4 {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .grid-4 {
    grid-template-columns: repeat(4, 1fr);
  }
}

.flex {
  display: flex;
}
.flex-center {
  justify-content: center;
  align-items: center;
}
.flex-between {
  justify-content: space-between;
  align-items: center;
}
.flex-column {
  flex-direction: column;
}
.flex-wrap {
  flex-wrap: wrap;
}

