.shadow {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
.shadow-lg {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.rounded {
  border-radius: 5px;
}
.rounded-lg {
  border-radius: 10px;
}
.rounded-circle {
  border-radius: 50%;
}

.bg-cover {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.overflow-hidden {
  overflow: hidden;
}

.position-relative {
  position: relative;
}

.position-absolute {
  position: absolute;
}

.w-100 {
  width: 100%;
}

.h-100 {
  height: 100%;
}

.d-none {
  display: none;
}

.d-block {
  display: block;
}

.d-inline-block {
  display: inline-block;
}

@media (min-width: 768px) {
  .d-md-none {
    display: none;
  }
  .d-md-block {
    display: block;
  }
  .d-md-inline-block {
    display: inline-block;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fade-in {
  animation: fadeIn 0.5s ease-in-out;
}

.bg-primary {
  background-color: #800020;
  color: #ffffff;
}

.bg-secondary {
  background-color: #ffd700;
  color: #800020;
}

.bg-light {
  background-color: #f8f8f8;
}

.bg-white {
  background-color: #ffffff;
}

.border {
  border: 1px solid rgba(74, 74, 74, 0.1);
}
.border-top {
  border-top: 1px solid rgba(74, 74, 74, 0.1);
}
.border-bottom {
